<template>
    <div class="houseType" id='2' >
         <dl>
             <dt></dt>
             <dd v-for="(item, index) in houseSell" :key='index'>
                 <span>{{item.size }}</span>
                 <span>{{item.size }}</span>
                 <span>{{ item.pri }}</span>
                 <span>({{ item.ave }}/)</span>
             </dd>
         </dl>
         <div class="expound">
             ,
         </div>
         <div @click = 'check' class="checkBot"></div>
    </div>
</template>


<script>
export default {
    data () {
        return{
            houseSell: [
                {
                    size: 88,
                    pri: 123.2,
                    ave: 14000,
                },
                {
                    size: 120,
                    pri: 168,
                    ave: 14000,
                },
                {
                    size: 103,
                    pri: 257.5,
                    ave: 25000,
                }
            ]
        }
    },
    methods: {
        check () {
              this.$store.state.fix = true;
              this.$store.state.tit1 = '';
              this.$store.state.tit2 = '';
              this.$store.state.word = ''
        }
    }
}
</script>


 <style lang="less" scoped>
   @import './../../assets/h5less/HouseType.less';
  </style>


