<template>
    <div class="people">
        <div class="proCon">
            <span></span>
            <span>11</span>
        </div>
        <div  @mouseenter="over"  @mouseleave="leave" class="boot" v-show='isShow'>
            <li class='el-icon-arrow-left' @click='clickLe'></li>
             <li class='el-icon-arrow-right' @mouseenter="over"  @mouseleave="leave" @click='clickRi'></li>
        </div>
        <div class="peoGo" :class="isPeo" @mouseenter="over"  @mouseleave="leave" >
        

          
                  <div  v-for="(peo, nub) in peoList" :key="nub" class="wrap">
                  <div class="wrap_left"><img  :src=peo.url alt=""></div>
        
                      <ul >
                          <li>{{ peo.name }}</li>
                          <li> {{ peo.work}}</li>
                           <li> :&emsp;&nbsp;<span>{{ peo.sati}}%</span></li>
                          <li> :&emsp;&emsp;&nbsp;<span>{{ peo.numb}}</span> </li>
                          <li> :<span> {{ peo.speed }}</span></li>
                      </ul>
                  <div class="warp_rig" @click='getAsk'>
                       <el-button size="mini"></el-button>
                  </div>
           
              </div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            isShow: false,
            isPeo: false,
            peoList: [
                {
                  url: require('../../../assets/picture/zx.jpg'),
                  name : '',
                  work: '',
                  sati: 96,
                  numb: 226,
                  speed: 11,
                },
                  {
                  url: require('../../../assets/picture/lz.jpg'),
                  name : '',
                  work: '',
                  sati: 91,
                  numb: 326,
                  speed: 11
                },  
                  {
                  url: require('../../../assets/picture/jc.jpg'),
                  name : '',
                  work: '',
                  sati: 91,
                  numb:222,
                  speed: 17
                },
                 {
                  url: require('../../../assets/picture/wm.jpg'),
                  name : '',
                  work: '',
                  sati: 91,
                  numb:222,
                  speed: 17
                }
            ]
        }
    },
    methods:{
        over () {
               this.isShow = true;
        },
        leave () {
            this.isShow = false;
        },
        clickLe () {
            this.isPeo = 'peoL';
        },
        clickRi () {
            this.isPeo = "peoR";
        },
        getAsk () {
              this.$store.state.show = true;
            this.$store.state.cont1 = '';
            this.$store.state.cont2 = '';
            this.$store.state.place = '';
            this.$store.state.button = '';
            this.$store.state.butTop = '';
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../../assets/webLess/ZhouTai/ShowPeople.less';
</style>


