<template>
    <div class="pic">
        <div class="picTop">
                <div class="picTop_left"></div>
                <div class="picTop_right"> 
                    <li class='el-icon-d-arrow-right'></li>
                </div>
        </div>
        <!--  -->
        <div class="picCon">
           <div class="picWrap">
                <div  @mouseenter="mouseover"  @mouseleave="mouseleave" class="butt" v-show='butShow'>
            <li class='el-icon-arrow-left' @click='clickLeft'></li>
             <li class='el-icon-arrow-right' @mouseenter="mouseover"  @mouseleave="mouseleave" @click='clickRight'></li>
        </div>
                <dl  @mouseenter="mouseover"  @mouseleave="mouseleave" :class='dl'>
                <dd v-for="(hou, hoIndex) in houseList" :key = 'hoIndex' >
                    <div class="hImg">
                        <img :src= hou.img alt="">
                    </div>
                    <div class="hShow">
                        <span> {{ hou.name }} </span>
                        <span> {{ hou.size }} </span>
                    </div>
                   
                    <div class="hBut"> {{ hou.pic  }} </div>
                 <div class="button" @click='getPic'>
                     <el-button size="mini">
                         <li class= 'el-icon-d-arrow-right'></li>
                     </el-button>
                </div>
                </dd>
            </dl>
   
           </div>
        </div>
    
    </div>
</template>


<script>
export default {
    data () {
        return {
            dl: null,
            butShow: null,
            houseList: [
                {
                    img: require('../../../assets/picture/house1.jpg'),
                    name: '',
                    size:  '  93',
                    pic: 260.40,
                },
                  {
                    img: require('../../../assets/picture/house2.jpg'),
                    name: '',
                    size:  '  103',
                    pic: 288.40,
                },
                  {
                    img: require('../../../assets/picture/house3.jpg'),
                    name: '',
                    size:  '  88',
                    pic: 123.20,
                },
                  {
                    img: require('../../../assets/picture/house4.jpg'),
                    name: '',
                    size:  '  93',
                    pic: 179.20,
                }
            ]
        }
    },
    methods: {
        mouseover  () {
            this.butShow = true;
        },
        mouseleave () {
            this.butShow = null;
        },
        clickLeft () {
            this.dl = 'dlLeft';
        },
        clickRight () {
            this.dl = 'dlRight';
        },
        getPic () {
            this.$store.state.show = true;
            this.$store.state.cont1 = '';
            this.$store.state.cont2 = '';
            this.$store.state.place = '';
            this.$store.state.button = '';
            this.$store.state.butTop = '';
        }
    }
}
</script>

<style lang="less" scoped>
 @import '../../../assets/webLess/ZhouTai/ShowPic.less';
</style>


