<template>
    <div class="data">
          <div class="dealDa"></div>
          <div class="dealNews">
              <div class="newsL">
                  <li class="el-icon-s-promotion"></li><span></span>
              </div>
              <div class="newR">
              <div class="newCon">
                 <span>{{ suName }}</span>
                 <span>{{ suPhone }}</span>
                 <span>{{  surRgion}}</span>
                 <span>{{ suDate }}</span>
                 <span>{{ suSu}}</span>
              </div>
              
              </div>
          </div>
          <!--  -->
          <div class="eartchs">
              <div id ="earLeft"></div>
              <div class="earRig">
                  <dl>
                      <dt>
                          <span></span>
                          <span></span>
                          <span></span>
                      </dt>
                      <dd v-for="(ite, index) in dataArr" :key='index'>
                          <span>{{ ite.time }}</span>
                          <span> {{ ite.houNum }}</span>
                          <span>{{ ite.houPic }}</span>
                      </dd>
                  </dl>
              </div>
          </div>
          <div class="bott" @click="getQue">
                <el-button size="mini" plain></el-button>
          </div>
    </div>
</template>


<script>
export default {
    data () {
        return {
            suName: '',
            suPhone: '',
             surRgion: '',
              suDate: '',
               suSu: '',
            dataArr: [
                {
                    time: '926',
                    houNum: 0 ,
                    houPic: ''
                },
                  {
                    time: '925',
                    houNum: 2 ,
                    houPic: '229'
                },
                  {
                    time: '924',
                    houNum: 0 ,
                    houPic: ''
                },
                  {
                    time: '923',
                    houNum: 5 ,
                    houPic: '207'
                },
                  {
                    time: '922',
                    houNum: 3 ,
                    houPic: '135'
                }
            ],
            dataList:[
                {
                  name: '',
                  phone: 17621471813,
                  region: ' ',
                  date: '0922',
                  sucss: ''
                },
                  {
                  name: '',
                  phone: 18621471813,
                  region: ' ',
                  date: '0922',
                  sucss: ''
                },
                  {
                  name: '',
                  phone: 15321471813,
                  region: ' ',
                  date: '0922',
                  sucss: ''
                },
                  {
                  name: '',
                  phone: 12421471813,
                  region: ' ',
                  date: '0922',
                  sucss: ''
                }
            ]
        }
    },
    mounted () {
            this.setIn();
            this.drawLine();
    },
    methods: {
       setIn () {
                let i = 0;
            this.suName = this.dataList[i].name.substr(0,1)+ '*'; 
        setInterval(() => {
       i++;
       if(i === this.dataList.length ){
           i = 0;
       }
       this.suName = this.dataList[i].name.substr(0,1)+ '*'; 
       this.suPhone = this.dataList[i].phone.toString().substr(0,3) + '****' + this.dataList[i].phone.toString().substr(7);
     
       this.surRgion = this.dataList[i].region;
       this.suDate = this.dataList[i].date;
       this.suSu = this.dataList[i].sucss;
     
        }, 1000);
    },
    drawLine () {
        let myChart = this.$echarts.init(document.getElementById('earLeft'));
        let option = {
         legend: {
             data: ['djdkdj']
         },
            color: ['#007aff'],
            grid: {
                left: '70px',
                // top: '30px'
            },
    xAxis: {
        type: 'category',
        data: ['4', '5', '6', '7', '8', '9']
    },
    yAxis: {
        scale: true,
        type: 'value',
        interval: 500,
        min: 12000,
        max: 16000
    },
    series: [{
        data: [14057, 14960, 13754, 14600, 14359, 14514],
        type: 'line'
    }],
};
   myChart.setOption(option)
    },
    getQue () {
             this.$store.state.show = true;
            this.$store.state.cont1 = '';
            this.$store.state.cont2 = '';
            this.$store.state.place = '';
            this.$store.state.button = '';
            this.$store.state.butTop = '';
    }
    }
}
</script>

<style lang="less" scoped>
 @import '../../../assets/webLess/ZhouTai/ShowEar.less';
</style>


