<template>
    <div class="housemore" id='0'>
        <div  class="houseImg">
    <van-swipe :autoplay="3000">
  <van-swipe-item v-for="(image, index) in phoneList" :key="index">
    <img v-lazy="image.url" />
  </van-swipe-item>
</van-swipe>
        </div>
        <div class="houseName">
         <H6></H6>
         <span></span>
        </div>
        <div class="onSell"></div>
        <div class="housePri">
            <span></span>
            <span>14000/</span>
            <span @click='changePri'></span>
        </div>
        <div class="houseAdr">
            <span></span>
            <span></span>
        </div>
        <div class="seeMore">
            <router-link to=''></router-link>
        </div>
    </div>
</template>

  <script>
  export default {
      data () {
          return {
               phoneList: [
                {
                    url: require('./../../assets/picture/play1.jpg')
                },
                {
                    url: require('./../../assets/picture/play2.jpg')
                },
                {
                    url: require('./../../assets/picture/play3.jpg')
                }
                ]
          }
      },
      methods: {
          changePri () {
              this.$store.state.fix = true;
              this.$store.state.tit1 = '';
              this.$store.state.tit2 = '';
              this.$store.state.word = ''
          }
      }
  }
  </script>


  <style lang="less" scoped>
   @import './../../assets/h5less/HouseMore.less';
  </style>


