<template>
    <div class="call">
        <div class="top">
            <div class="toLeft">
           <a href="tel:400-666-6666">
                  <p>400-666-6666</p>
              <p>,</p>
               <div class="ph">
                <li class='el-icon-phone'></li>
            </div>
           </a>
            </div>
        </div>
        <div class="bottom">
            <div class="botLeft">
                <li class='el-icon-chat-dot-square'></li>
                <span @click='makePeo'></span>
            </div>
            <div class="botRig">
                <li class='el-icon-truck'></li>
                <span @click='makeCar' ></span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    methods:{
        makePeo () {
             this.$store.state.fix = true;
              this.$store.state.tit1 = '';
              this.$store.state.tit2 = '';
              this.$store.state.word = '';
        },
        makeCar () {
              this.$store.state.fix = true;
              this.$store.state.tit1 = '';
              this.$store.state.tit2 = '';
              this.$store.state.word = ''
        }
    }
}
</script>

 <style lang="less" scoped>
   @import './../../assets/h5less/CallPhone.less';
  </style>


