<template>
    <div class="nan">
        <h6>(4)</h6>
       <div class="dl_wra">
           <dl>
           <dd v-for="(item, index) in nanList" :key='index'>
               <div class="img_wra">
                  <img :src=item.url alt="">
               </div>
               <div class="hType">
                   <span>{{item.size}}</span>
                   <span></span>
               </div>
               <div class="hSize">
                   <span></span>
                   <span>{{item.size}}</span>
               </div>
               <div class="pric">
                   {{item.pric}}
               </div>
           </dd>
       </dl>
       </div>
       <div @click = 'nanBot' class="nan_bottom">
           
       </div>
    </div>
</template>

<script>
export default {
    data () {
       return{
            nanList: [
            {
                url: require('../../assets/picture/fxs/hou1.jpg'),
                size: 88,
                pric: 23.2
            },
             {
                url: require('../../assets/picture/fxs/hou3.jpg'),
                size: 120,
                pric: 168
            },
             {
                url: require('../../assets/picture/fxs/hou2.jpg'),
                size: 103,
                pric: 257.5
            },
             {
                url: require('../../assets/picture/fxs/hou4.jpg'),
                size: 125,
                pric: 175
            }
        ]
       }
    },
    methods: {
        nanBot () {
              this.$store.state.fix = true;
              this.$store.state.tit1 = '';
              this.$store.state.tit2 = '';
              this.$store.state.word = ''
        }
    }
}
</script>


  <style lang="less" scoped>
   @import './../../assets/h5less/HouseNan.less';
  </style>


