<template>
    <div class="ShowHome">
      <!--  -->
             <div class="block">
    <el-carousel trigger="click" height='500px'>
      <el-carousel-item v-for="(item, index) in picList" :key="index">
     <img :src="item.url" alt="">
      </el-carousel-item>
    </el-carousel>
  </div>
    <!--  -->
        <div class="information">
                <H6></H6>
                <div class="activity">
                    <span ></span>
                    <span></span>
                    <span @click='seeHome'> <li class='el-icon-d-arrow-right'></li> </span>
                </div>
    <!--  -->
            <div class="inforPic">
                <span>{{ picture }}/m</span>
                <span>{{ picFlo }}  </span> <li class='el-icon-top'></li>
                <span @click ='getPri'> <li class ='el-icon-d-arrow-right'></li> </span>
            </div>
    <!--  -->
            <div class="buttDis">
                <el-row>
  <el-button size="small" plain v-for="(bot, num) in botList" :key='num'>{{ bot }}</el-button>
</el-row>
            </div>
            <div class="gps">
                <span>[] </span>
                <span> <li class='el-icon-d-arrow-right'></li></span>
            </div>
          <div class="couCar">
                <!--  -->
            <div class="coupon" @click='getCou'>
                <div class="couLeft">
                    <div class="couMon">10000</div>
                    <div class="couTime">102</div>
                </div>
                <div class="couRight" ></div>
            </div>
            <!--  -->
             <div class="special" @click='getSpe'>
                <div class="speLeft">
                    <div class="speCar">VIP</div>
                    <div class="spePeo"></div>
                </div>
                <div class="speRight"></div>
            </div>
   
          </div>
          <!--  -->
          <div class="houseType">
                 <dl>
                     <dt></dt>
                     <dd v-for="( house, hIndex) in houseList" :key='hIndex'>
                         <span>{{ house.houName }}</span>
                         <span> {{ house.houSize }} </span>
                         <span> {{ house.houPic }} </span>
                         <span> {{ house.avePic }}/m </span>
                         <span> {{ house.dealPic }}</span>
                     </dd>
                 </dl>
                 <div class="houNud" @click='getHou'>
                     
                 </div>
             </div>
        </div>
    </div>
</template>


<script>
export default {
    data () {
        return {
            picture: 14000,
            picFlo: '',
            botList:['','','',''],
            picList:[
                {
                    url: require('../../../assets/picture/pic1.jpg')
                },
                {
                    url: require('../../../assets/picture/pic2.jpg')
                },
                {
                    url: require('../../../assets/picture/pic3.jpg')
                },
                {
                    url: require('../../../assets/picture/pic4.jpg')
                },
                {
                    url: require('../../../assets/picture/pic5.jpg')
                },
                {
                    url: require('../../../assets/picture/pic6.jpg')
                },
                {
                    url: require('../../../assets/picture/pic7.jpg')
                }
            ],
            houseList:[
                {
                    houName: '',
                    houSize: 93,
                    houPic: 260,
                    avePic: 28000,
                    dealPic: ""
                },
                 {
                    houName: '',
                    houSize: 103,
                    houPic: 288,
                    avePic:  28000,
                    dealPic:  ""
                },
                 {
                    houName: '',
                    houSize: 88,
                    houPic: 123,
                    avePic: 14000,
                    dealPic: ""
                },
               {
                    houName: '',
                    houSize: 128,
                    houPic: 179,
                    avePic: 14000,
                    dealPic: ""
                }
            ]
        }
    },
    methods: {
        seeHome () {
            this.$store.state.show = true;
            this.$store.state.cont1 = '';
            this.$store.state.cont2 = '';
            this.$store.state.place = ',';
            this.$store.state.button = '';
        },
        getPri () {
            this.$store.state.show = true;
            this.$store.state.cont1 = '';
            this.$store.state.cont2 = '';
            this.$store.state.place = ',';
            this.$store.state.button = '';
        },
        getCou () {
              this.$store.state.show = true;
            this.$store.state.cont1 = '10000';
            this.$store.state.cont2 = '10000';
            this.$store.state.place = ',';
            this.$store.state.button = '';
        },
        getSpe ()  {
             this.$store.state.show = true;
            this.$store.state.cont1 = '';
            this.$store.state.cont2 = 'VIP';
            this.$store.state.place = ',';
            this.$store.state.button = '';
        },
        getHou () {
              this.$store.state.show = true;
            this.$store.state.cont1 = '';
            this.$store.state.cont2 = '';
            this.$store.state.place = '';
            this.$store.state.button = '';
            this.$store.state.butTop = '';
        }
    }
}
</script>



<style lang="less">
@import '../../../assets/webLess/ZhouTai/ShowHome.less';
// @import '../../../assets/webLess/ZhouTai/ShowHomeH5.less';
</style>



