<template>
    <div class="chioce">
        <div class="chTop">
            <span></span>
            <li class='el-icon-arrow-right'></li>
            <span></span>
        </div>
        <!--  -->
        <div class="region">
            <dl v-for="(item, index) in findList" :key='index'>
                <dt>{{ item.name}}</dt>
                <dd :class="{'dCor': isCor==word}" @click='click(word)' v-for="(son, word) in item.children" :key='word'>
                    {{ son }}
                </dd>
            </dl>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
             isCor: '',
            findList: [
                {
                    name: "",
                    children:['','', '' , '', '','', '' , '', '','', '' , '']
                },{
                    name: '',
                    children: ['','<100','100-120', '120-150', '150-200', '200-300', '300-500', '>500'] 
                },
                {
                    name: '',
                    children: ['','','','','','']
                },
                {
                    name: '',
                    children:['','','','','','','','','','','']
                },
                {
                    name: '',
                    children: ['','','','','','','','','','','','LOFT','','']
                },
                {
                    children:['','','','','','','','','']
                }
            ]
        }
    },
    methods: {
        click (word) {
            this.isCor = word
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/webLess/LouPan/LouChioce.less';
</style>


