

<template>
    <div class="position">
        <div class="pis_button">
            <div class="pis_left">
                <img :src="imgUrl" alt="">
            </div>
             <div class="pis_con">
                <div class='pis_con_top'></div>
                <div class='pis_con_bottom'></div>
            </div>
            <div class="pis_rig">
                <div class='inputTop'></div>
                <div class="input">
                   <el-input type='text' maxlength="11" onkeyuo="this.value=this.value.replace(/\D/g,'')" v-model="input" placeholder=""></el-input>    
                   <el-button size="small"></el-button>
                </div>
                <div class="phone">: <span>400-666-6666</span></div>
            </div>
        </div>
    </div>
</template>



<script>
export default {
    data () {
        return {
            input: '',
           imgUrl: require('../../../assets/picture/posi1.png')
        }
    }
}
</script>

<style lang="less">
 @import '../../../assets/webLess/ZhouTai/ZhouFooter.less';
</style>


